import React from 'react';
import QRCode from 'qrcode.react';
import { makeStyles } from '@material-ui/core';

function Qrcode({allSettings}) {
  const {keyNameBg,keyNameImage} = allSettings;

  const useStyles = makeStyles(theme => ({
    root: {
      width:"100%",
      height:"110vh",
      minHeight:"100%",
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
      background:`url(${keyNameBg})`,
      backgroundPosition:"center center",
      backgroundSize:"cover",
      padding: theme.spacing(2),
  
  
      '& .MuiTextField-root': {
        margin: theme.spacing(1),
        width: '300px',
      },
      '& .MuiButtonBase-root': {
        margin: theme.spacing(2),
        color: "black"
      },
    },
    keyNameImage:{
      width:"95%",
      maxWidth:"400px"
    }
  }));
  const classes = useStyles();

  return (
    <div className={classes.root} >
      <div className={classes.keyNameImage}>
        <img style={{width:"100%"}} src={keyNameImage}/>
      </div>
    <div className="qrcode_container">
      <QRCode
      includeMargin="true"
      size={180}
       value="https://namesinfunctions.web.app/keyname"
       />
    </div>
    </div>

  )
}

export default Qrcode;

